import { lazy } from "react";
import MinterContent from "../../content/MinterContent.json";
import AboutContent from "../../content/AboutContent.json";
import RarityContent from "../../content/RarityContent.json";
import FAQcontent from "../../content/FAQcontent.json";
import TeamContent from "../../content/TeamContent.json";

const MiddleBlock = lazy(() => import("../../components/MiddleBlock"));
const ImageBlock = lazy(() => import("../../components/ImageBlock"));
const Container = lazy(() => import("../../common/Container"));
const ScrollToTop = lazy(() => import("../../common/ScrollToTop"));
const ContentBlock = lazy(() => import("../../components/ContentBlock"));
const OurTeamBlock = lazy(() => import("../../components/OurTeamBlock"));

const Home = () => {
  return (
    <Container>
      <ScrollToTop />
      <ContentBlock
        type="right"
        title={MinterContent.title}
        content={MinterContent.text}
        button={MinterContent.button}
        id="intro"
      />
      <MiddleBlock
        title={AboutContent.title}
        content={AboutContent.text}
      />
      <ImageBlock image_path="/img/png/Roadmap.png"/>
      <ContentBlock
        type="right"
        title={RarityContent.title}
        content={RarityContent.text}
        id="mission"
      />
      <ContentBlock
        type="left"
        title={FAQcontent.title}
        content={FAQcontent.text}
        id="product"
      />
      <OurTeamBlock></OurTeamBlock>
    </Container>
  );
};

export default Home;
